<template>
  <div v-loading="loading">
    <div v-if="!loading">
      <!--渲染表单-->
      <form-render class="process-form" ref="form" :forms="forms" v-model="formData"/>
    </div>
    <el-divider>审批流程</el-divider>
    <div>
      <!--渲染流程执行过程-->

      <FlowProcessNodeShow :formData="formData" ref="flowProcessNodeRef" @childEvent="selectNodeUser" processInstanceId="" :key="k"
                           :row="form.nodeShow"></FlowProcessNodeShow>

    </div>
  </div>
</template>

<script>
import FormRender from '@/views/common/form/FormRender'
import {getFormDetail} from '@/api/design'
import {formatStartNodeShow} from '@/api/processInstance'
import FlowProcessNodeShow from '@/components/common/FlowProcessNodeShow'

export default {
  name: "InitiateProcess",
  components: {FormRender, FlowProcessNodeShow},
  props: {
    code: {
      type: String,
      required: true
    },
    defaultFormData: {
      type: Object,
      default: () => {
        return {}
      }

    }
  },
  data() {
    return {
      formDataChangeTime: new Date().getTime(),
      loading: false,
      formData: {},
      form: {
        formId: '',
        formName: "",
        logo: {},
        formItems: [],
        process: {},
        remark: "",
        nodeShow: []
      },
      nodeDataUser: {},k:1
    }
  },
  watch: {
    //监听路由地址的改变
    formData: {
      deep: true,
      handler(v) {
        var t = this;
        this.formDataChangeTime = new Date().getTime();
        setTimeout(function () {
          if (new Date().getTime() - t.formDataChangeTime > 500) {
            this.formDataChangeTime = new Date().getTime();
            t.getNodeShow();
          }
        }, 600);
      }
    }
  },
  mounted() {
    var t = this
    this.loadFormInfo(this.code, function () {
      if (t.defaultFormData) {
        for (var k in t.defaultFormData) {

          t.$set(t.formData, k, t.defaultFormData[k]);
        }
      }
    })

  },
  computed: {
    forms() {
      var formItems = this.$store.state.design.formItems;

      return formItems;
    }
  },
  methods: {

    getNodeShow() {
      var t = this;

      formatStartNodeShow({
        formId: t.code, paramMap: t.formData
      }).then(res => {

        t.form.nodeShow = res.data;

        t.k=t.k+1

      })


    },

    selectNodeUser(r, b) {

      this.$set(this.formData,b+'_assignee_select',r[b])

      this.$emit("childEvent", r, b);
    },


    loadFormInfo(formId, func) {
      this.loading = true
      getFormDetail(formId).then(rsp => {
        this.loading = false

        let form = rsp.data;
        form.logo = JSON.parse(form.logo)
        form.settings = JSON.parse(form.settings)
        // form.nodeShow = (form.nodeShow)
        //  form.selectUserNodeId = (form.selectUserNodeId)
        form.formItems = JSON.parse(form.formItems)
        form.process = JSON.parse(form.process)
        this.form = form
        //构建表单及校验规则
        this.$store.state.design = form

        this.$emit("childEvent1");

        //发起表单权限
        var formPerms = form.process.props.formPerms;

        for (var fp of formPerms) {
          var fid = fp.id;
          var perm = fp.perm;
          if (this.forms) {
            for (var f of this.forms) {
              if (f.id === fid) {
                f.props.perm = perm;
                break
              }
            }
          }

        }
        //默认值
        var formItems = form.formItems;
        for (var fi of formItems) {
          var v = fi.props.value;
          if (this.$isNotBlank(v)) {
            this.$set(this.formData, fi.id, v);
          }
        }

        //回调
        if (func) {
          func();
        }

      }).catch(err => {
        this.loading = false
        this.$message.error(err)
      })
    },
    validate(call) {
      this.$refs.form.validate(call);
    }
  }
}
</script>

<style lang="less" scoped>
.process-form {
  /deep/ .el-form-item__label {
    padding: 0 0;
  }
}
</style>
