import request from '@/api/request.js'


// 启动实例
export function startProcess(param) {
  return request({
    url: 'process-instance/startProcessInstance',
    method: 'post',
    data: param
  })
}

// 查询当前用户待办任务
export function queryMineTask(param) {
  return request({
    url: 'process-instance/queryMineTask',
    method: 'post',
    data: param
  })
}


// 查询当前用户已办任务
export function queryMineEndTask(param) {
  return request({
    url: 'process-instance/queryMineEndTask',
    method: 'post',
    data: param
  })
}

// 查询当前用户发起的流程
export function queryMineStarted(param) {
  return request({
    url: 'process-instance/queryMineStarted',
    method: 'post',
    data: param
  })
}

// 查询抄送任务
export function queryMineCC(param) {
  return request({
    url: 'process-instance/queryMineCC',
    method: 'post',
    data: param
  })
}

// 查询用户单个任务
export function queryTask(param) {
  return request({
    url: 'task/queryTask',
    method: 'get',
    params: param
  })
}

// 退回
export function back(param) {
  return request({
    url: 'task/back',
    method: 'post',
    data: param
  })
}
//实例显示图片
export function showImg(procInsId) {
  return request({
    url: 'process-instance/showImg?procInsId='+procInsId,
    method: 'get'
  })
}



// 完成任务
export function completeTask(param) {
  return request({
    url: 'task/completeTask',
    method: 'post',
    data: param
  })
}

// 前加签
export function delegateTask(param) {
  return request({
    url: 'task/delegateTask',
    method: 'post',
    data: param
  })
}


// 加签完成任务
export function resolveTask(param) {
  return request({
    url: 'task/resolveTask',
    method: 'post',
    data: param
  })
}


// 结束流程
export function stopProcessInstance(param) {
  return request({
    url: 'task/stopProcessInstance',
    method: 'post',
    data: param
  })
}


//  后加签
export function setAssignee(param) {
  return request({
    url: 'task/setAssignee',
    method: 'post',
    data: param
  })
}


// 查询抄送详细信息
export function queryMineCCDetail(param) {
  return request({
    url: 'processCopy/querySingleDetail',
    method: 'get',
    params: param
  })
}

//  发起时渲染下面节点
export function formatStartNodeShow(param) {
  return request({
    url: 'process-instance/formatStartNodeShow',
    method: 'post',
    data: param
  })
}


//  流程详情
export function detail(param) {
  return request({
    url: 'process-instance/detail',
    method: 'get',
    params: param
  })
}

export default {
  formatStartNodeShow,stopProcessInstance,detail,queryMineEndTask,startProcess,queryMineTask,queryTask,completeTask,queryMineStarted,queryMineCC,queryMineCCDetail,showImg,delegateTask,resolveTask,setAssignee,back
}
