<template>
    <div>


        <el-dialog width="80%" title="流程图片" :visible.sync="showDialogControl">
            <img :src="imgBase64" style="width: 100%;"/>
</el-dialog>


    </div>
</template>

<script>

import { showImg} from '@/api/processInstance'

export default {
    name: "workSpace",
    components: {  },
    data() {
        return {
            showDialogControl:false,
            imgBase64:''
        }
    },
    props:{

    },
    mounted() {
    },
    computed: {},
    methods: {
            showDialog(processInstanceId){
                var t=this;
                this.showDialogControl=true;
                showImg(processInstanceId).then(res=>{
                    t.imgBase64='data:image/png;base64,'+res.data
                })
            }
    }
}
</script>

