<template>
  <div>


    <el-drawer destroy-on-close :title="drawerTitle" :visible.sync="showRight" direction="rtl">

      <div style="padding: 5px;background-color: lightgrey" class="right-drawer-1">
        <el-card class="box-card" style="margin-top: 10px;">

          <form-render class="process-form" ref="form" :forms="formItems" v-model="formData"/>

        </el-card>
        <el-card class="box-card" style="margin-top: 15px;">
          <FlowProcessNodeShow :processInstanceId="processInstanceId" :row="nodeShow"></FlowProcessNodeShow>

        </el-card>


      </div>
      <div class="right-drawer-2" v-if="this.showOperation">

        <el-button  @click="showApproveDialog(false)"
                   type="danger">驳回
        </el-button>
        <el-button   @click="showApproveDialog(true)" type="primary">提交</el-button>
      </div>
    </el-drawer>

    <!--前加签-->

    <add-before ref="addBeforeHandler" @ok="done"></add-before>

    <!--后加签-->

    <add-after ref="addAfterHandler" @ok="done"></add-after>

    <!--同意审批-->
    <agree ref="agreeHandler" @ok="done"></agree>


    <!--驳回审批-->
    <reject ref="rejectHandler" @ok="done"></reject>

  </div>
</template>

<script>
import FormRender from '@/views/common/form/FormRender'
import FlowProcessNodeShow from '@/components/common/FlowProcessNodeShow'
import {
  queryTask,
  formatStartNodeShow,detail
} from '@/api/processInstance'
import Agree from './handler/agree.vue'
import Reject from './handler/reject.vue'
import AddAfter from './handler/addAfter.vue'
import AddBefore from './handler/addBefore.vue'

export default {
  name: "workSpace",
  components: {  FlowProcessNodeShow, FormRender,Agree,Reject,AddAfter,AddBefore},
  props: {},
  data() {
    return {
      showOperation:true,
      processInstanceId: '',


      showRight: false,
      taskId: '',
      nodeId: '',

      formItems: [],
      formData: {},
      rowData: {},
      nodeShow: [],
      drawerTitle: '',
      delegationTask: false,
      delegateAgain: false,
      process:{}
    }
  },
  mounted() {
  },
  watch: {
    //监听路由地址的改变
    formData: {
      deep: true,
      handler(v) {
        var t = this;
        this.formDataChangeTime = new Date().getTime();
        setTimeout(function () {
          if (new Date().getTime() - t.formDataChangeTime > 500) {

            t.getNodeShow();
          }
        }, 600);
      }
    }
  },
  computed: {
  },
  methods: {

    getNodeShow() {
      formatStartNodeShow({
        taskId: this.taskId,
        processInstanceId: this.processInstanceId,
        paramMap: this.formData
      }).then(res => {
        this.nodeShow = res.data;
      })
    },






    showAddBeforeSign() {
      this.$refs.addBeforeHandler.show(this.taskId)
    },
    showAddAfterSign() {
      this.$refs.addAfterHandler.show(this.taskId)
    },
    show(row,showOperation) {
      var t = this;

      if(showOperation!=undefined){
        this.showOperation=showOperation;
      }

      t.taskId = row.taskId
      t.nodeId = row.nodeId;
      t.currentFlowId = row.flowId;
      t.processInstanceId = row.processInstanceId;
      t.drawerTitle = row.processName;
      this.handleClick(row)

      this.getNodeShow();
    },
    showOperationBtn(i) {
      if (!this.node) {
        return false
      }
      if(!this.showOperation){
        return false;
      }
      if (!this.node.props) {
        return false;
      }
      if (!this.node.props.operationBtn) {
        return false;
      }
      return this.node.props.operationBtn.indexOf(i) > -1
    },

    showApproveDialog(val) {


      this.$refs.form.validate(valid => {
        if (valid) {

          this.formData[this.node.id + '_approve_condition'] = val

            this.$refs.agreeHandler.show(this.taskId,this.delegationTask,this.formData);


        } else {
          this.$message.warning("请完善表单")
        }
      })


    },
    done(){
      this.showRight = false;
      this.$emit("back", 1);

    },

    handleClick(row) {
      var t = this;
      t.taskId = row.taskId
      t.nodeId = row.nodeId;
      t.drawerTitle = row.processName;
      if(!this.$isNotBlank(t.drawerTitle)){
        t.drawerTitle = row.name;

      }


      t.currentFlowId = row.flowId;

      if(!this.$isNotBlank(row.taskId)){
        //任务不存在

        detail({processInstanceId: row.processInstanceId}).then(res => {

          t.node = res.data.node;

          t.delegationTask = res.data.delegationTask
          t.delegateAgain = res.data.delegateAgain
          t.process = JSON.parse(res.data.process)

          for (var item of res.data.formItems) {
            // t.formData[item.id]=item.value;
            t.$set(t.formData, item.id, item.value)
          }


          t.formItems = res.data.formItems
          t.showRight = true;

          t.processInstanceId = res.data.processInstanceId;

        }).catch(err => {
          this.$message.error(err.response.data)
        })

        return null;
      }

      queryTask({taskId: row.taskId}).then(res => {

        t.node = res.data.node;

        t.delegationTask = res.data.delegationTask
        t.delegateAgain = res.data.delegateAgain
        t.process = JSON.parse(res.data.process)

        for (var item of res.data.formItems) {
          // t.formData[item.id]=item.value;
          t.$set(t.formData, item.id, item.value)
        }


        t.formItems = res.data.formItems
        t.showRight = true;

        t.processInstanceId = res.data.processInstanceId;

      }).catch(err => {
        this.$message.error(err.response.data)
      })

    },


  }
}
</script>

<style lang="less" scoped>
.process-form {
  /deep/ .el-form-item__label {
    padding: 0 0;
  }
}

.right-drawer-1 {
  height: calc(100vh - 140px);
  overflow-y: hidden;
  overflow-y: scroll;
}

.right-drawer-2 {
  height: 60px;
  border: 0px solid black;
  background-color: white;
  line-height: 60px;
  text-align: center;

}

/deep/ .el-drawer {
  min-width: 500px !important;
}
</style>
