<template>
    <div>


        <el-table :data="list" border style="width: 100%">
            <el-table-column prop="groupName" label="分组" width="180">
            </el-table-column>
            <el-table-column prop="processName" label="流程" width="180">
            </el-table-column>
            <el-table-column prop="startUserName" label="发起人" width="180">
            </el-table-column>
            <el-table-column prop="startTime" label="流程发起时间" width="180">
            </el-table-column>
            <el-table-column prop="nodeName" label="当前节点" width="180">
            </el-table-column>
            <el-table-column prop="nodeTime" label="抄送时间" width="180">
            </el-table-column>
            <el-table-column fixed="right" label="操作">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                    <el-button @click="showPic(scope.row)" type="text" size="small">流程图</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="text-align: center;padding: 20px;">
            <el-pagination background @size-change="handleSizeChangePending"
                           @current-change="handleCurrentChangePending"
                           layout="total, sizes, prev, pager, next, jumper" :page-sizes="[10, 20, 50]"
                           :page-size="pageData.count"
                           :current-page="pageData.currentPage" :total="total">
            </el-pagination>
        </div>


        <el-drawer destroy-on-close :title="drawerTitle" :visible.sync="showRight" direction="rtl">

            <div style="padding: 5px;background-color: lightgrey" class="right-drawer-1">
                <el-card class="box-card" style="margin-top: 10px;">

                    <form-render class="process-form" ref="form" :forms="formItems" v-model="formData"/>

                </el-card>



            </div>

        </el-drawer>

        <showProcessInstanceImg ref="showProcessInstanceImg"  ></showProcessInstanceImg>

    </div>
</template>

<script>
import FormRender from '@/views/common/form/FormRender'
import {queryMineCC, queryMineCCDetail} from '@/api/processInstance'
import showProcessInstanceImg from './showProcessInstanceImg.vue'
import FormHandler from "@/views/workspace/component/handler.vue";
export default {
    name: "workSpace",
    components: {FormHandler, FormRender,showProcessInstanceImg},
    data() {
        return {

            showRight: false,
            drawerTitle: '',

            formItems: [],
            formData: {},
            list: [],
            total: 0,
            pageData: {
                count: 10,
                currentPage: 1
            }
        }
    },
    mounted() {
        this.queryList();
    },
    computed: {},
    methods: {
        showPic(row) {
            this.$refs.showProcessInstanceImg.showDialog(row.processInstanceId);
        },

        handleClick(row) {

            console.log(row)

            var t = this;

            queryMineCCDetail({ id: row.id }).then(res => {
                t.drawerTitle = row.processName;


                for (var item of res.data.formItems) {
                    // t.formData[item.id]=item.value;
                    t.$set(t.formData, item.id, item.value)
                }



                t.formItems = res.data.formItems
                t.showRight = true;


            }).catch(err => {
                this.$message.error(err.response.data)
            })




        },

        handleSizeChangePending(val) {

            this.pageData.count = val;
            this.pageData.currentPage = 1;
            this.queryList();
        },
        handleCurrentChangePending(val) {

            this.pageData.currentPage = val;
            this.queryList();
        },

        queryList() {
            var t = this;
            queryMineCC({page: this.pageData.currentPage, count: this.pageData.count}).then(res => {
                t.list = res.data.records;
                t.total = res.data.total;
            }).catch(err => {
                this.$message.error(err.response.data)
            })
        },
    }
}
</script>

<style lang="less" scoped>
.process-form {
  /deep/ .el-form-item__label {
    padding: 0 0;
  }
}

.right-drawer-1 {
  height: calc(100vh - 80px);
  overflow-y: hidden;
  overflow-y: scroll;
}


</style>
