<template>
  <div>

    <!--同意审批-->
    <el-dialog destroy-on-close title="提交" :visible.sync="visible" width="500px">
      <el-button size="mini" type="primary" @click="showSelectUserDialog">选择加签人</el-button>
      <org-items v-model="orgPickerSelected"/>

<!--      <el-switch style="margin-top: 10px;"-->
<!--                 v-model="delegate"-->
<!--                 active-text="允许转发"-->
<!--                 inactive-text="不允许转发">-->
<!--      </el-switch>-->
      <el-form>
        <el-form-item label="" label-width="200">
          <el-input maxlength="200" rows="5" show-word-limit type="textarea" v-model="desc"
                    placeholder="提交意见" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="visible = false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>

    <org-picker title="选择用户" type="user" ref="orgPicker" :selected="orgPickerSelected"
                @ok="selected"/>

  </div>
</template>

<style scoped lang="less">

</style>
<script>


import {delegateTask} from "@/api/processInstance";
import OrgItems from "@/views/common/process/OrgItems.vue";
import OrgPicker from "@/components/common/OrgPicker.vue";


export default {
  name: "workSpace",
  components: {OrgPicker, OrgItems},
  props: {},
  data() {
    return {
      delegate:false,
      visible: false,
      desc: '',
      taskId:'',
      orgPickerSelected:[]
    }
  },
  mounted() {
  },
  watch: {},
  computed: {},
  methods: {
    selected(select) {

      this.orgPickerSelected.length = 0
      select.forEach(val => this.orgPickerSelected.push(val))
    },
    showSelectUserDialog(){
      this.$refs.orgPicker.show()

    },
    show(taskId){
      this.taskId=taskId
      this.visible=true;
    },
    submit(){
      var param = {
        targetUserId: this.orgPickerSelected[0].id,

        taskId: this.taskId,
        taskLocalParamMap: {
          delegate: this.delegate,
          approveDesc: this.desc
        }

      };
      this.complete(param);
    },
    complete(param) {

      delegateTask(param).then(res => {
        this.orgPickerSelected.splice(0,this.orgPickerSelected.length)

        this.visible = false

        this.desc=''

        this.$emit("ok");

      }).catch(err => {
        this.$message.error(err.response.data)
      })
    },

  }
}
</script>
