<template>
  <div>

    <!--驳回审批-->
    <el-dialog destroy-on-close title="提交" :visible.sync="visible" width="500px">
      <el-form>
        <el-form-item label="驳回节点" label-width="200">
          <el-select v-model="targetNodeId" placeholder="驳回节点" style="width: 100%;">
            <el-option v-for="(node, i) in rejectNodeList" :key="i" :label="node.name" :value="node.id"></el-option>

          </el-select>
        </el-form-item>
        <el-form-item label="" label-width="200">
          <el-input maxlength="200" rows="5" show-word-limit type="textarea" v-model="desc"
                    placeholder="提交意见" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="visible = false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<style scoped lang="less">

</style>
<script>


import {back, completeTask, resolveTask} from "@/api/processInstance";


export default {
  name: "workSpace",
  components: {},
  props: {},
  data() {
    return {
      visible: false,
      desc: '',
      formData:{},
      taskId:'',
      nodeId:'',
      targetNodeId:'',
      process:{}
    }
  },
  mounted() {
  },
  watch: {},
  computed: {
    rejectNodeList() {
      //驳回节点列表
      let values = []
      const excType = ['APPROVAL','ROOT']

      var arr = {

      }
      var obj = {}


      this.produceSerialNodeList(undefined,this.process, arr, obj)


      var k = arr[this.nodeId];

      if(k==undefined){
        return []
      }

      for (var item of k) {
        var type = obj[item].type;
        if(type==='CONCURRENTS'||type==='INCLUSIVES'){
          values=[];
        }
        if (excType.indexOf(type)>-1&&obj[item].id!=this.nodeId) {
          values.push({ id: obj[item].id, name: obj[item].name })
        }
      }


      return values
    }

  },
  methods: {
    produceSerialNodeList(parentId, process, nodeArr, nodeObj) {

      if (!this.$isNode(process)) {
        return;
      }




      var nodeId = process.id;

      nodeObj[nodeId] = process;


      if (!this.$isNotBlank(parentId)) {
        var arr = []
        arr.push(nodeId)
        nodeArr[nodeId] = arr;
      } else {
        var p = nodeArr[parentId];
        var arr1 = this.$deepCopy(p);
        arr1.push(nodeId)
        nodeArr[nodeId] = arr1;
      }


      var type = process.type;

      var children = process.children;

      if (type === 'CONDITIONS' || type === 'CONCURRENTS' || type === 'INCLUSIVES') {

        var branchs = process.branchs;



        for (var item of branchs) {

          this.produceSerialNodeList(nodeId, item, nodeArr, nodeObj)

        }

        if (this.$isNode(children)) {

          this.produceSerialNodeList(nodeId, children, nodeArr, nodeObj)

        }

      } else {
        if (this.$isNode(children)) {
          this.produceSerialNodeList(nodeId, children, nodeArr, nodeObj)
        }
      }

    },

    show(taskId,delegationTask,formData,process,nodeId){
      this.taskId=taskId
      this.delegationTask=delegationTask
      this.formData=formData
      this.process=process
      this.nodeId=nodeId

      if(this.rejectNodeList.length<1){
        this.$message.error("无可驳回节点")
        return
      }
      this.targetNodeId=this.rejectNodeList[this.rejectNodeList.length-1].id

      this.visible=true;
    },
    submit(){
      var formData = this.formData;

      let desc = this.desc;

      var param = {
        paramMap: formData,
        taskId: this.taskId,
        targetNodeId: this.targetNodeId,
        taskLocalParamMap: {
          approveDesc: desc
        }

      };

      this.complete(param);
    },
    complete(param) {
      back(param).then(res=>{

        this.visible = false

        this.desc =  ''

        this.$emit("ok");
      }).catch(err => {
        this.$message.error(err.response.data)

      })
    },

  }
}
</script>
