<template>
    <div>


        <el-table :data="list" border style="width: 100%">
            <el-table-column prop="groupName" label="分组" width="180">
            </el-table-column>
            <el-table-column prop="name" label="流程" width="180">
            </el-table-column>
            <el-table-column prop="createTime" label="流程发起时间" width="180">
            </el-table-column>
            <el-table-column prop="endTime" label="流程结束时间" width="180">
            </el-table-column>
            <el-table-column label="状态" width="180">
                <template slot-scope="scope">
                    <el-tag v-if="scope.row.status == 1" type="success">进行中</el-tag>
                    <el-tag v-else>已结束</el-tag>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">

                  <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                  <el-button v-if="scope.row.status == 1" @click="stop(scope.row)" type="text" size="small">终止流程</el-button>
                    <el-button @click="showPic(scope.row)" type="text" size="small">流程图</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="text-align: center;padding: 20px;">
            <el-pagination background @size-change="handleSizeChangePending" @current-change="handleCurrentChangePending"
                layout="total, sizes, prev, pager, next, jumper" :page-sizes="[10, 20, 50]" :page-size="pageData.count"
                :current-page="pageData.currentPage" :total="total">
            </el-pagination>
        </div>

      <form-handler @back="initList" ref="formHandler"></form-handler>


      <showProcessInstanceImg ref="showProcessInstanceImg"  ></showProcessInstanceImg>
    </div>
</template>

<script>

import { queryMineStarted, stopProcessInstance } from '@/api/processInstance'
import showProcessInstanceImg from './showProcessInstanceImg.vue'
import FormHandler from "@/views/workspace/component/handler.vue";
export default {
    name: "workSpace",
    components: {FormHandler, showProcessInstanceImg},
    data() {
        return {
            showRight: false,
            drawerTitle: '',
            formItems: [],
            formData: {

            },
            list: [],
            total: 0,
            pageData: {
                count: 10,
                currentPage: 1
            },
            nodeShow: []
        }
    },
    mounted() {
        this.queryList();
    },
    computed: {

    },
    methods: {
      initList(page){

      },
      handleClick(row) {

        this.$refs.formHandler.show(row,false);


      },
        stop(row){
            stopProcessInstance({
                processInstanceId:row.processInstanceId}).then(res=>{
                this.queryList();
            })
        },
        showPic(row) {
            this.$refs.showProcessInstanceImg.showDialog(row.processInstanceId);
        },


        handleSizeChangePending(val) {

            this.pageData.count = val;
            this.pageData.currentPage = 1;
            this.queryList();
        },
        handleCurrentChangePending(val) {

            this.pageData.currentPage = val;
            this.queryList();
        },

        queryList() {
            var t = this;
            queryMineStarted({ page: this.pageData.currentPage, count: this.pageData.count }).then(res => {
                t.list = res.data.records;
                t.total = res.data.total;
            }).catch(err => {
                this.$message.error(err.response.data)
            })
        },
    }
}
</script>

<style lang="less" scoped>
.process-form {
    /deep/ .el-form-item__label {
        padding: 0 0;
    }
}
</style>
