<template>
  <div>
    <el-timeline :reverse="false">
      <el-timeline-item v-for="(node, index) in row" :key="index" size="large" :color="node.status!=2?(
        node.status==1?'green':'grey'
      ):'blue'"
                        :icon="nodeIconShow(node.type)">


          <p>{{ node.name }}
            <template v-if="node.placeholder&&node.placeholder.length>0">[{{ node.placeholder }}]</template>
          </p>
          <div v-if="node.userDtoList&&node.userDtoList.length>0">
            <el-card class="box-card" v-for="(item1,index1) in node.userDtoList" :key="index1"
                     style="margin-bottom: 10px;">

              <div v-for="(item,index) in item1" class="node-show" :key="index">
                <div style="overflow: hidden;">
                  <div class="d1">
                    <div>
                      <el-avatar shape="square" :size="30" :src="item.avatar"></el-avatar>
                      <i v-if="item.status===2" style="color: white;background-color: red"
                         class="el-icon-finished avatar-icon"></i>
                      <i v-if="item.status===1" style="color: white;background-color: green"
                         class="el-icon-time avatar-icon"></i>
                    </div>
                    <div
                        style="font-size: 8px;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;text-align: left">
                      {{ item.name }}
                    </div>
                  </div>
                  <div class="d2">
                    <div>{{ item.showTime }}</div>
                    <div>
                      <el-tag v-if="item.operType==='COMPLETE'" type="success">完成</el-tag>
                      <el-tag v-if="item.operType==='DELEGATION'" type="danger">委派</el-tag>
                      <el-tag v-if="item.operType==='RESOLVED'" type="warning">解决</el-tag>
                    </div>
                  </div>
                </div>


                <p v-if="item.approveDesc&&item.approveDesc.length>0"
                   style="text-align: left;background-color: lightgrey;padding: 10px 10px;">{{ item.approveDesc }}</p>

              </div>
            </el-card>
          </div>

          <template v-if="node.selectUser">

            <div style="flex-direction: row;display: flex;">

              <div v-for="(a, b) in nodeUser[node.id]" style="width: 50px;height:50px;" :key="b">
                <el-avatar shape="square" :size="30" :src="a.avatar"></el-avatar>


                <div
                    style="text-align: left;font-size: 8px;width:50px;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;">
                  {{ a.name }}
                </div>
              </div>
              <div
                  v-if="(!processInstanceId||processInstanceId.length==0)"
                  style="width: 50px;height:50px;">
                <i
                    @click="showSelectUser(node)"
                    class="el-icon-circle-plus-outline self-select-user-icon"
                    :style="{ color: (nodeUser[node.id] && nodeUser[node.id].length > 0) ? 'black' : 'red' }">
                </i>

              </div>
            </div>



          </template>

            <el-tabs v-if="node.branch.length>0" type="border-card">
              <el-tab-pane v-for="(node1, index1) in node.branch" :label="'分支'+(index1+1)" :name="index1+''" :key="index1">
                <template v-if="node1.placeholder&&node1.placeholder.length>0">[{{ node1.placeholder }}]</template>
                <div style="padding:0px 5px;">
                  <flow-process-node-show :formData="formData" ref="flowProcessNodeRef" @childEvent="selectNodeUser"
                                          :processInstanceId="processInstanceId"
                                          :row="node1.children"></flow-process-node-show>

                </div>
              </el-tab-pane>

            </el-tabs>


      </el-timeline-item>
    </el-timeline>


    <org-picker title="选择用户" :multiple="multipleSelectUser" type="user" ref="orgPicker"
                :selected="orgPickerSelected"
                @ok="selected"/>

  </div>
</template>

<script>

import Constant from '../../utils/Constant'
import OrgPicker from "@/components/common/OrgPicker.vue";

export default {
  name: "flow-process-node-show",
  components: {OrgPicker},
  props: {
    row: {
      type: Array,
      default: () => []
    },
    formData: {
      type: Object,
      default: () => {
      }
    },
    processInstanceId: {
      type: String, default: ''
    }
  },

  computed: {
    nodeUser() {
      var formData = this.formData;
      if (!formData) {
        return {}
      }
      var data = this.$deepCopy(formData);

      console.log(data)

      var obj = {}

      for (var item in data) {
        var indexOf = item.indexOf('_assignee_select');
        if (indexOf >= 0) {

          obj[item.substring(0, indexOf)] = data[item]

        } else {
          obj[item] = data[item]
        }
      }
      console.log(obj)
      return obj

    },
  },
  mounted() {
  },
  data() {
    return {
      currentNodeId: undefined,
      // nodeUser: {
      //
      // },
      multipleSelectUser: false,
      orgPickerSelected: [],
    }
  },
  methods: {

    showSelectUser(node) {
      this.currentNodeId = node.id
      this.multipleSelectUser = node.multiple ? node.multiple : false
      var nodeUserElement = this.nodeUser[node.id];


      this.orgPickerSelected.length=0;
      if(nodeUserElement){
        for(var item of nodeUserElement){
          this.orgPickerSelected.push(item)
        }
      }

      console.log("要显示的用户",this.orgPickerSelected)

      this.$refs.orgPicker.show()

    },
    selected(select) {

      this.orgPickerSelected.length = 0
      select.forEach(val => this.orgPickerSelected.push(val))
      this.nodeUser[this.currentNodeId] = this.orgPickerSelected;


      this.$emit("childEvent", this.nodeUser, this.currentNodeId);
    },
    selectNodeUser(v, b) {
      this.$emit("childEvent", v, b);
    },
    nodeIconShow(type) {

      var param = Constant.nodeParam[type];
      return param == undefined ? '' : param.icon
    }
  }
}
</script>

<style lang="less" scoped>


@variable_node_left_width: 200px;


.node-show {


  .d1 {
    float: left;
    width: @variable_node_left_width;
    height: 50px;
  }

  .d2 {
    float: left;
    height: 50px;
    text-align: right;
    width: calc(100% - @variable_node_left_width - 8px);
  }
}

.avatar-icon {
  position: absolute;
  margin-left: -13px;
  margin-top: 17px;
  border-radius: 30%;
}

.self-select-user-icon {
  font-size: 40px;
  cursor: pointer;
  display: inline-block;
}

</style>
