<template>
  <div>


    <el-table :data="list" border style="width: 100%">
      <el-table-column prop="groupName" label="分组" width="180">
      </el-table-column>
      <el-table-column prop="processName" label="流程" width="180">
      </el-table-column>
      <el-table-column prop="rootUserName" label="发起人" width="180">
      </el-table-column>
      <el-table-column prop="startTime" label="流程发起时间" width="180">
      </el-table-column>
      <el-table-column prop="taskName" label="任务名称" width="180">
      </el-table-column>
      <el-table-column prop="taskCreateTime" label="任务开始时间" width="180">
      </el-table-column>
      
      <el-table-column prop="taskEndTime" label="任务结束时间" width="180">
      </el-table-column>
      <el-table-column fixed="right" label="操作">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
          <el-button @click="showPic(scope.row)" type="text" size="small">流程图</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align: center;padding: 20px;">
      <el-pagination background @size-change="handleSizeChangePending" @current-change="handleCurrentChangePending"
                     layout="total, sizes, prev, pager, next, jumper" :page-sizes="[10, 20, 50]"
                     :page-size="pageData.count"
                     :current-page="pageData.currentPage" :total="total">
      </el-pagination>
    </div>


    <form-handler @back="initList" ref="formHandler"></form-handler>


    <showProcessInstanceImg ref="showProcessInstanceImg"></showProcessInstanceImg>
  </div>
</template>

<script>
import {queryMineEndTask} from '@/api/processInstance'
import showProcessInstanceImg from './showProcessInstanceImg.vue'
import FormHandler from './handler.vue'

export default {
  name: "workSpace",
  components: {showProcessInstanceImg, FormHandler},
  data() {
    return {


      list: [],
      total: 0,
      pageData: {
        count: 10,
        currentPage: 1
      },
    }
  },
  mounted() {
    this.queryList();
  },
  computed: {},
  methods: {

    showPic(row) {
      this.$refs.showProcessInstanceImg.showDialog(row.processInstanceId);
    },


    handleClick(row) {

      this.$refs.formHandler.show(row,false);


    },

    handleSizeChangePending(val) {

      this.pageData.count = val;
      this.pageData.currentPage = 1;
      this.queryList();
    },
    handleCurrentChangePending(val) {

      this.pageData.currentPage = val;
      this.queryList();
    },
    initList(page){
      this.pageData.currentPage = page;
      this.queryList();
    },
    queryList() {
      var t = this;
      queryMineEndTask({page: this.pageData.currentPage, count: this.pageData.count}).then(res => {
        t.list = res.data.records;
        t.total = res.data.total;
      }).catch(err => {
        this.$message.error(err.response.data)
      })
    },
  }
}
</script>

<style lang="less" scoped>
.process-form {
  /deep/ .el-form-item__label {
    padding: 0 0;
  }
}

.right-drawer-1 {
  height: calc(100vh - 140px);
  overflow-y: hidden;
  overflow-y: scroll;
}

.right-drawer-2 {
  height: 60px;
  border: 0px solid black;
  background-color: white;
  line-height: 60px;
  text-align: center;

}
</style>
