<template>
  <div>

    <!--同意审批-->
    <el-dialog destroy-on-close title="提交" :visible.sync="visible" width="500px">
      <el-form>
        <el-form-item label="" label-width="200">
          <el-input maxlength="200" rows="5" show-word-limit type="textarea" v-model="desc"
                    placeholder="提交意见" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="visible = false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<style scoped lang="less">

</style>
<script>


import {completeTask, resolveTask} from "@/api/processInstance";


export default {
  name: "workSpace",
  components: {},
  props: {},
  data() {
    return {
      visible: false,
      desc: '',
      delegationTask:false,
      formData:{},
      taskId:''
    }
  },
  mounted() {
  },
  watch: {},
  computed: {},
  methods: {
    show(taskId,delegationTask,formData){
      this.taskId=taskId
      this.delegationTask=delegationTask
      this.formData=formData
      this.visible=true;
    },
    submit(){
      var formData = this.formData;

      let desc = this.desc;

      var param = {
        paramMap: formData,
        taskId: this.taskId,
        taskLocalParamMap: {
          approveDesc: desc
        }

      };

      this.complete(param);
    },
    complete(param) {
      var axiosPromise;
      if (this.delegationTask) {
        axiosPromise = resolveTask(param);

      } else {
        axiosPromise = completeTask(param);

      }
      axiosPromise.then(res => {

        this.visible = false

        this.desc=''

        this.$emit("ok");

      }).catch(err => {
        this.$message.error(err.response.data)
      })
    },

  }
}
</script>
