<template>
    <div>

        <el-dialog :title="dialogTitle" width="800px" :visible.sync="showDialog" :close-on-click-modal="false">
            <initiate-process ref="processForm" :defaultFormData="defaultFormData" @childEvent="selectNodeUser" @childEvent1="removeNodeUser" :code="formId"
                v-if="showDialog"></initiate-process>
            <span slot="footer" class="dialog-footer">
                <el-button size="mini" @click="showDialog = false">取 消</el-button>
                <el-button size="mini" type="primary" @click="submitForm">提 交</el-button>
            </span>
        </el-dialog>

    </div>
</template>

<script>
import InitiateProcess from "../InitiateProcess";
import FormRender from '@/views/common/form/FormRender'
export default {
    name: "workSpace",
    components: { FormRender, InitiateProcess },
    props: {
        dialogTitle: {
            type: String,
            default: '发起流程'
        },
        formId: String,
        paramId:String
    },
    data() {
        return {
            nodeDataUser: {},
            showDialog: false,
            defaultFormData:{

            }
        }
    },
    mounted() {

    },
    computed: {

    },
    methods: {
        openDialog(defaultData) {
            if(defaultData){
                   this.defaultFormData=defaultData;
            }
            this.showDialog = true;
        },

        closeDialog() {
            this.showDialog = false;
        },
        selectNodeUser(r, b) {

            this.nodeDataUser[b] = r[b];
        },
        removeNodeUser() {

            this.nodeDataUser = {}
        },
        submitForm() {
            let formData = this.$refs.processForm.formData;
            let form = this.$refs.processForm.form;


            var t = this;

            //
            // for (let key in this.nodeDataUser) {
            //     var tt = this.nodeDataUser[key];
            //     if (tt === undefined || tt == null) {
            //         continue
            //     }
            //
            //     formData[key + '_assignee_select'] = tt;
            // }

            var completeNodeUserSelect = true;
            var selectUserNodeId = form.selectUserNodeId;

            for (var k of selectUserNodeId) {
                var d = this.nodeDataUser[k]
                if (d && d.length > 0) {

                } else {
                    completeNodeUserSelect = false;
                    break;
                }
            }

            if (!completeNodeUserSelect) {
                this.$message.error('请完善节点信息')
                return;
            }


            this.$refs.processForm.validate(valid => {
                if (valid) {
                    t.$emit('submitProcess',t.formId,formData,t.paramId)

                } else {
                    this.$message.warning("请完善表单😥")
                }
            })
        }
    }
}
</script>

<style lang="less" scoped></style>
